<template>
  <div class="main">
    <div class="wrapper">
      <h2>用户管理</h2>
      <div class="button-wrapper"></div>
      <el-card class="box-card">
        <el-button style="float: right" @click="createNewUser">
          <i class="el-icon-plus"></i>添加
        </el-button>
        <div class="table-wrap">
          <el-table :data="tableData">
            <el-table-column type="index"></el-table-column>
            <el-table-column prop="name" label="用户名" width="180"></el-table-column>
            <el-table-column prop="roles" label="权限" width="180">
              <template slot-scope="scope">{{ getRoleName(scope.row.roles) }}</template>
            </el-table-column>
            <el-table-column prop="clazz" label="班级" width="180">
              <template slot-scope="scope">{{ getClassName(scope.row.clazz) }}</template>
            </el-table-column>
            <el-table-column label="操作">
              <template slot-scope="scope">
               
                <el-button size="mini" @click="editUser(scope.row)">编辑</el-button>
                <el-button size="mini" type="danger" @click="handleDelete(scope.row)">删除</el-button>
              </template>
            </el-table-column>
          </el-table>
        </div>
        <div class="block">
          <el-pagination
            layout="prev,pager,next"
            @current-change="currentChange"
            :current-page="currentPage"
            :total="total"
            :page-size="pageSize"
            background
          ></el-pagination>
        </div>
      </el-card>
      <el-dialog :visible.sync="dialogFormShow" :title="dialogStatus[flag]" center width="500px">
        <el-form ref="dialog" :model="dialog" :rules="checkRules()" size="mini">
          <el-form-item label="用户名" prop="name">
            <el-input v-model="dialog.name"></el-input>
          </el-form-item>
          <el-form-item label="密码" prop="password">
            <el-input v-model="dialog.password" type="password"></el-input>
          </el-form-item>
          <el-form-item label="确认密码" prop="checkPass">
            <el-input v-model="dialog.checkPass" type="password"></el-input>
          </el-form-item>
          <el-form-item label="权限" prop="roles">
            <el-select v-model="dialog.roles" multiple value-key="id" placeholder="请选择">
              <el-option v-for="role in roles" :key="role.id" :label="role.role_name" :value="role"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="班级" prop="clazz">
            <el-select v-model="dialog.clazz" placeholder="请选择">
              <el-option
                v-for="clazz in classes"
                :key="clazz.id"
                :label="clazz.name"
                :value="clazz.id"
              ></el-option>
            </el-select>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button type="primary" @click="flag==='edit'? handleUpdate():handleCreate()">提交</el-button>
          <el-button type="primary" @click="dialogFormShow=false">取消</el-button>
        </div>
      </el-dialog>
    </div>
  </div>
</template>

<script>
import {
  getAllUser,
  createUser,
  updateUser,
  deleteUser,
  getAllRoles
} from "@/api/user";
//import { getAllGrade } from "@/api/grade";
import store from "@/store/index";

export default {
  name: "userManage",

  data() {
    var validatePass = (rule, value, callback) => {
      let pattern = /^[\w_-]{6,16}$/;
      if (!pattern.test(value)) {
        callback(new Error("密码不符合"));
      } else {
        if (this.dialog.checkPass !== "") {
          this.$refs.dialog.validateField("checkPass");
        }
        callback();
      }
    };

    var validateEdit = (rule, value, callback) => {
      if (value && value.length < 8) {
        callback(new Error("密码不符合"));
      } else {
        if (this.dialog.checkPass !== "") {
          this.$refs.dialog.validateField("checkPass");
        }
        callback();
      }
    };

    var validatePass2 = (rule, value, callback) => {
      if (this.flag === "create") {
        if (value !== null && value === "") {
          callback(new Error("请再次输入密码"));
        } else if (value !== this.dialog.password) {
          callback(new Error("两次输入密码不一致!"));
        } else {
          callback();
        }
      } else {
        if (value !== this.dialog.password) {
          callback(new Error("两次输入密码不一致!"));
        } else {
          callback();
        }
      }
    };

    return {
      tableData: [],
      currentPage: 1,
      pageSize: 5,
      total: undefined,

      roles: [],
      role_id: undefined,

      classes: [{ id: -2, name: "无" }, ...store.getters.classes],

      dialog: {
        id: undefined,
        name: "",
        roles: [],
        clazz: undefined,
        password: "",
        checkPass: ""
      },

      dialogFormShow: false,
      dialogStatus: {
        create: "添加新用户",
        edit: "修改用户信息"
      },

      flag: "",
      btn_stu: false,

      editRule: {
        name: [{ required: true, message: "请输入用户名", trigger: "change" }],
        password: [{ validator: validateEdit, trigger: "change" }],
        checkPass: [{ validator: validatePass2, trigger: "change" }],
        roles: [{ required: true, message: "请选择角色", trigger: "change" }],
        clazz: [{ required: true, message: "请选择班级", trigger: "change" }]
      },
      createRule: {
        name: [{ required: true, message: "请输入用户名", trigger: "blur" }],
        password: [{ validator: validatePass, trigger: "blur" }],
        checkPass: [{ validator: validatePass2, trigger: "blur" }],
        roles: [{ required: true, message: "请选择角色", trigger: "change" }],
        clazz: [{ required: true, message: "请选择班级", trigger: "change" }]
      }
    };
  },
  created() {
    this.initUser();
    this.initRoles();
    //this.getAllClasses();
  },

  computed: {
    name: function() {
      return store.getters.name;
    }
  },

  methods: {
    initUser() {
      getAllUser(this.currentPage, this.pageSize).then(resp => {
        this.tableData = resp.data.data;
        this.total = resp.data.total;
      });
    },

    initRoles() {
      getAllRoles().then(resp => {
        this.roles = [...resp.data];
      });
    },

    checkRules() {
      if (this.flag === "edit") {
        return this.editRule;
      } else {
        return this.createRule;
      }
    },

    getClassName(clazzId) {
      for (var i in this.classes) {
        if (this.classes[i].id == clazzId) {
          return this.classes[i].name;
        }
      }
    },

    getRoleName(roles) {
      return roles.map(role => role.role_name);
    },

    currentChange(page) {
      this.currentPage = page;
      this.initUser();
    },

    resetDialog() {
      this.dialog = {
        id: undefined,
        name: "",
        roles: [],
        clazz: undefined,
        password: "",
        checkPass: ""
      };
    },

    createNewUser() {
      this.dialogFormShow = true;
      this.flag = "create";
      this.resetDialog();
      this.$nextTick(() => {
        this.$refs["dialog"].clearValidate();
      });
    },

    handleCreate() {
      this.$refs["dialog"].validate(valid => {
        if (valid) {
          createUser(this.dialog)
            .then(resp => {
              if (resp.status === 200) {
                this.dialogFormShow = false;
                if(this.total%this.pageSize===0){
                  this.total+=1
                }
                this.dialog.id = resp.data.obj
                this.tableData.unshift(this.dialog);
                this.$message({
                  type: "success",
                  message: "添加成功"
                });
              }
            })
            .catch(() => {
              this.$message.error("添加失败");
            });
        } else {
          return false;
        }
      });
    },

    editUser(row) {
      this.dialogFormShow = true;
      this.flag = "edit";
      // this.$nextTick(() => {
      //   this.$refs["dialog"].resetFields();
      // });
      row = { ...row, password: "", checkPass: "" };
      this.dialog = Object.assign({}, row);
      this.dialog.clazz = row.clazz;
      this.$nextTick(() => {
        this.$refs["dialog"].clearValidate();
      });
    },

    handleUpdate() {
      this.$refs["dialog"].validate(valid => {
        if (valid) {
          const dialogData = Object.assign({}, this.dialog);
          if (dialogData.password === "") {
            dialogData.password = null;
          }
          updateUser(dialogData)
            .then(() => {
              for (const v of this.tableData) {
                if (v.id === this.dialog.id) {
                  const index = this.tableData.indexOf(v);
                  this.tableData.splice(index, 1, this.dialog);
                  break;
                }
              }
              this.dialogFormShow = false;
              this.$message({
                message: "修改成功",
                type: "success"
              });
            })
            .catch(() => {
              this.$message({
                message: "修改失败",
                type: "error"
              });
            });
        } else {
          return false;
        }
      });
    },

    handleDelete(row) {
      this.$confirm("是否删除", "提示", {
        comfirmButtonText: "确定",
        cancelButtonText: "取消"
      }).then(() => {
        if (this.name === row.name) {
          this.$message({
            message: "无法删除当前用户"
          });
        } else {
          deleteUser(row.id)
            .then(() => {
              this.$message({
                type: "success",
                message: "删除成功"
              });
              const index = this.tableData.indexOf(row);
              this.tableData.splice(index, 1);
              if ( this.tableData.length == 0) {
                this.currentPage = this.currentPage - 1;
                this.initUser();
              }
            })
            .catch(() => {
              this.$message({
                type: "error",
                message: "删除失败"
              });
            });
        }
      });
    }
  }
};
</script>